import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import * as THREE from 'three';
import { SkeletonUtils } from 'three/examples/jsm/utils/SkeletonUtils';
import * as CANNON from "cannon-es";
import { Character } from './character';
var loader = new GLTFLoader();
export default {
    loadEnv: function () {
        return loader.loadAsync("/env.gltf").then(function (gltf) {
            var model = gltf.scene;
            model.scale.setScalar(10);
            return model;
        });
    },
    loadCharacter: function (charInfo) {
        return loader.loadAsync("/".concat(charInfo.name, ".gltf")).then(function (gltf) {
            var halfExtents = charInfo.halfExtents;
            var model = gltf.scene;
            var body = new CANNON.Body({
                mass: charInfo.mass,
                shape: new CANNON.Box(new CANNON.Vec3(halfExtents.x, halfExtents.y, halfExtents.z)),
                collisionResponse: false,
                collisionFilterGroup: charInfo.collisionGroup,
                collisionFilterMask: charInfo.collisionTarget
            });
            model.scale.setScalar(charInfo.scale);
            //Animation
            console.log("ANIMATIONs");
            console.log(charInfo.name);
            console.log(gltf.animations);
            var animations = [];
            if (gltf.animations && gltf.animations.length > 0) {
                console.log("Set has animation");
                var mixer = new THREE.AnimationMixer(model);
                animations = gltf.animations;
                charInfo.set_has_anim(true);
                for (var idx in animations) {
                    var clipAction = mixer.clipAction(animations[idx]);
                    clipAction.play();
                }
                // let mixer = new THREE.AnimationMixer( gltf.scene );
                // var action = mixer.clipAction( gltf.animations[ 0 ] );
                // action.play();
                charInfo.set_mixer(mixer);
            }
            return new Character(charInfo, body, model, animations);
        });
    },
    cloneCharacter: function (character) {
        console.log("cloneCharacter");
        var info = character.info.clone();
        var halfExtents = info.halfExtents;
        var animations = character.animation.slice();
        var model = SkeletonUtils.clone(character.model);
        info.set_mixer(new THREE.AnimationMixer(model));
        console.log(animations);
        for (var idx in animations) {
            var clipAction = info.mixer.clipAction(animations[idx]);
            clipAction.play();
        }
        var body = new CANNON.Body({
            mass: info.mass,
            shape: new CANNON.Box(new CANNON.Vec3(halfExtents.x, halfExtents.y, halfExtents.z)),
            collisionResponse: false,
            collisionFilterGroup: info.collisionGroup,
            collisionFilterMask: info.collisionTarget
        });
        return new Character(info, body, model, animations);
    }
};
